  border-radius: 10px;

  #stage {
    position: relative;
    width: 304px;
    height: 304px;
    border: 2px solid #000;

    // 蛇的主体部分
    #snake {
      & > div {
        position: absolute;
        width: 10px;
        height: 10px;
        background-color: #000;
        border: 1px solid @bg-color;
      }
    }

    & > #food {
      display: flex;
      flex-wrap: wrap;
      justify-content: space-between;
      align-content: space-between;
      position: absolute;
      left: 100px;
      top: 200px;
      width: 10px;
      height: 10px;

      & > div {
        width: 4px;
        height: 4px;
        background-color: #000;
        transform: rotate(45deg);
      }
    }
  }

  #score-panel {
    width: 300px;
    display: flex;
    justify-content: space-between;
  }
}

#score-panel {
  font: bold 20px "Courier";
}
